<template>
  <div style="padding: 0">
    <h2 class="center"></h2>
    <Form hide-required-mark ref="formItem" :model="formItem" :rules="fromValidate" :label-width="80">
      <FormItem label="" prop="username">
        <Input v-model="formItem.username" placeholder="2-16"></Input>
      </FormItem>
      <FormItem label="" prop="password">
        <Input type="password" password v-model="formItem.password" placeholder="8-32, "></Input>
      </FormItem>
      <div class="center">
        <Button style="margin-right: 10px" @click="resetForm('formItem')"></Button>
        <Button type="primary" @click="submitForm('formItem')"></Button>
      </div>
    </Form>
  </div>
</template>

<script>
export default {
  name: "DriverRegister",
  data() {
    return {
      formItem: {
        username: '',
        password: ''
      },
      fromValidate: {
        username: [
          {required: true, message: '', trigger: 'blur'},
        ],
        password: [
          {required: true, message: '', trigger: 'blur'},
          {type: 'string', min: 8, message: '8', trigger: 'blur'},
          {type: 'string', max: 32, message: '32', trigger: 'blur'},
          {pattern: /(?=.*[0-9])(?=.*[a-zA-Z])/, message: '', trigger: 'blur'}
        ],
      }
    }
  },
  props: ['registerInfo'],
  methods: {
    submitForm(name) {
      var that = this
      this.$refs[name].validate((valid) => {
        if (valid) {
          var data = {
            username: this.formItem.username,
            password: this.formItem.password,
            fleetId: this.registerInfo.fleetId
          }
          this.$axios(
              {
                method: 'post',
                url: `${this.$baseURI}/api/admin/hotel/add`,
                data: data
              }
          ).then(function (response) {
            if (response['data']['success'] === true) {
              that.$Message.success("");
              setTimeout(function () {
                that.$emit('setRegisterModal', false);
                that.resetForm(name)
              }, 1500);
            } else {
              that.$Message.error(response['data']['message'])
            }
          })
        }
      })
    },
    resetForm(name) {
      this.$refs[name].resetFields();
    },
  }
}
</script>

<style scoped>
@media screen and (max-width: 640px) {
  .form-wrapper {
    width: 90%;
    margin: auto;
  }
}

.center {
  text-align: center;
}
</style>