<template>
  <div>
    <div>
      <Button type="primary" @click="showRegisterModal"></Button>
      <Input suffix="ios-search" style="width: auto; margin-left: 15px"
             placeholder="" v-model='keyword'/>
    </div>
    <Table :columns="this.columns" :data="this.fleetInfo.driverData" :stripe="true"
           style="margin: 30px auto; overflow-y: auto">
    </Table>
    <Modal style="padding: 20px"
           footer-hide
           :mask-closable="false"
           v-model="registerModal">
      <DriverRegister @setRegisterModal=setRegisterModal
                      :registerInfo="registerInfo"></DriverRegister>
    </Modal>
  </div>
</template>

<script>
import DriverRegister from "@/components/admin/DriverRegister";

export default {
  name: "DriverDetail",
  components: {DriverRegister},
  data() {
    return {
      columns: [
        {
          title: '',
          key: 'driverId',
          align: 'center',
        },
        {
          title: '',
          key: 'name',
          align: 'center',
        },
        {
          title: '',
          key: 'username',
          align: 'center',
        },
        {
          title: '',
          key: 'accountId',
          align: 'center',
        },
        {
          title: '',
          key: 'operation',
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'error',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.deleteDriver(params.row.driverId)
                  }
                }
              }, ''),
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {

                  }
                }
              }, ''),
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {

                  }
                }
              }, ''),
            ]);
          }
        },
      ],
      searching: false,
      keyword: '',
      driverDataBackup: [],
      registerModal: false,
      registerInfo: {
        fleetId: 0,
      }
    }
  },
  props: ['fleetInfo'],
  methods: {
    deleteDriver(driverId) {
      console.log(driverId)
    },
    showRegisterModal() {
      this.registerInfo.fleetId = this.fleetInfo.fleetId
      this.registerModal = true
    },
    setRegisterModal(fromChild) {
      this.registerModal = fromChild;
    },
  },
  watch: {
    keyword() {
      //
      let that = this
      if (this.timer) {
        clearTimeout(this.timer)
      }
      //  
      if (this.keyword === '') {
        that.fleetInfo.driverData = that.driverDataBackup.slice(0)
        that.searching = false
        return
      }
      this.timer = setTimeout(() => {
        if (!that.searching) {
          that.driverDataBackup = that.fleetInfo.driverData.slice(0)
        }
        that.searching = true
        that.fleetInfo.driverData = []
        that.driverDataBackup.forEach(v => {
          if ((v.name + v.driverId + v.accountId + v.username).indexOf(this.keyword) > -1) {
            that.fleetInfo.driverData.push(v)
          }
        })
      }, 100)
    }
  },
}
</script>

